

import Vue from 'vue'
import App from './App'
import router from './router'
import store from '@/store';
import RemLayout from '@/assets/js/remLayout'
import axios from 'axios'
import FastClick from 'fastclick'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

Vue.prototype.$ajax=axios 

Vue.use(ConfirmPlugin)
Vue.use(AlertPlugin)
Vue.use(ToastPlugin)
Vue.use(LoadingPlugin)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
el: '#app',
router,
store,
components: { App },
template: '<App/>'
})

if (process.env.NODE_ENV === 'production') {

FastClick.attach(document.body);
}


window.API_ORIGIN = (process.env.IS_PRE || process.env.NODE_ENV === "development") ?


'http://yimei.huabeicat.cn' :
'http://yimei.huabeimao.net';


RemLayout(750);



